import config from '../config/config'

export default {
    layout: 'social',
    data() {
        return {
            user: null,
            loading: false,
            isDisable: false,
        }
    },
    head: {
        title: 'Tài khoản' + config.siteName,
            htmlAttrs: {
            lang: 'en'
        },
    },
    methods: {
        async updatePorfile() {
            try {
                if (!this.isDisable) {
                    this.loading = true
                    let params = {
                        avatar: this.user.avatar,
                        name: this.user.name,
                        new_password: this.user.new_password,
                        old_password: this.user.old_password,
                    }
                    await this.$http.post('/v2/update-profile', params)
                    this.loading = false
                    this.$notify.success({
                        title: 'Success',
                        message: 'Cập nhật thành công'
                    });
                }
            } catch (error) {
                this.loading = false
                this.$notify.error({
                    title: 'Error',
                    message: error.response.data.message
                });
            }
        }
    },
    watch: {
        '$store.state.user': function() {
            this.user = {...this.$store.state.user}
        }
    },
    mounted() {
        let user = localStorage.getItem('user_info') ? JSON.parse(localStorage.getItem('user_info')) : null;
        if (!user || !user?.api_key) {
            this.isDisable = true
        } else {
            this.user = {...this.$store.state.user}
        }
    }
}
